import React from "react";
import styled from "styled-components";

const Container = styled.div`
  width: 100%;
`;
const ButtonContainer = styled.div`
  width: 100%;
  height: 40px;
  background-color: rgba(10, 10, 10, 0.8);
  display: flex;
`;

const Button = styled.button`
  cursor: pointer;
  width: 100%;

  background-color: ${(props) =>
    props.selected ? "#c8224f" : "rgba(20, 20, 20, 0.8)"};
`;

export default function DetailTabHeader({
  isVideos,
  isCompanies,
  isCountries,
  isSeasons,
  handleClick,
  selectedButton,
}) {
  //   console.log(isVideos);
  //   console.log(isCompanies);
  //   console.log(isCountries);
  //   console.log(isSeasons);
  //   console.log(selectedButton);

  return (
    <Container>
      <ButtonContainer>
        {isVideos && (
          <Button
            selected={selectedButton === "Youtube"}
            onClick={(e) => handleClick("Youtube")}
          >
            Youtube
          </Button>
        )}
        {isCompanies && (
          <Button
            selected={selectedButton === "Company"}
            onClick={(e) => handleClick("Company")}
          >
            Company
          </Button>
        )}
        {isCountries && (
          <Button
            selected={selectedButton === "Country"}
            onClick={(e) => handleClick("Country")}
          >
            Country
          </Button>
        )}
        {isSeasons && (
          <Button
            selected={selectedButton === "Seasons"}
            onClick={(e) => handleClick("Seasons")}
          >
            Seasons
          </Button>
        )}
      </ButtonContainer>
    </Container>
  );
}
