import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Loading from "Components/Loading";
import Helmet from "react-helmet";
import DetailTabContainer from "Components/DetailTab/DetailTabContainer";

const Container = styled.div`
  position: relative;
  height: calc(105vh - 50px);
  width: 100%;
  padding: 50px;
`;

const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url(${(props) => props.bgImage});
  background-position: center, center;
  background-size: cover;
  filter: blur(2px);
  opacity: 0.5;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  height: calc(120vh - 100px);
  position: relative;
`;

const Cover = styled.img`
  top: 80px;
  width: 30%;
  height: calc(100vh - 150px);
  background-size: cover;
  background-position: center, center;
  border-radius: 5px;
  position: sticky;
`;

const Data = styled.div`
  width: 70%;
  margin-left: 10px;
  display: block;
`;
const Header = styled.div`
  display: flex;
`;

const Title = styled.h3`
  font-size: 35px;
`;

const IMDB = styled.a``;

const Img = styled.img`
  margin-top: 6px;
  margin-left: 10px;
  background-image: url(${(props) => props.bgImage});
  width: 52px;
  height: 30px;
  background-position: center, center;
`;

const ItemContainer = styled.div`
  margin: 20px 0;
`;

const Item = styled.span``;

const Divider = styled.span`
  margin: 0 10px;
`;

const Overview = styled.p`
  font-size: 12px;
  line-height: 1.5;
  opacity: 0.7;
  width: 60%;
  height: 100px;
  margin-bottom: 30px;
  display: block;
`;

const DetailPresenter = ({ result, error, loading, isMovie }) => {
  return loading ? (
    <Loading nowPage="Detail" />
  ) : (
    <Container>
      <Helmet>
        <title>
          {result.original_title ? result.original_title : result.original_name}
        </title>
      </Helmet>
      <Backdrop
        bgImage={
          result.backdrop_path
            ? `https://image.tmdb.org/t/p/original${result.backdrop_path}`
            : result.poster_path
            ? `https://image.tmdb.org/t/p/original${result.poster_path}`
            : require("assets/happyPig.jpg")
        }
      />

      <Content>
        <Cover
          src={
            result.poster_path
              ? `https://image.tmdb.org/t/p/original${result.poster_path}`
              : require("assets/happyPig.jpg")
          }
        />
        <Data>
          <Header>
            <Title>
              {result.original_title
                ? result.original_title
                : result.original_name}
            </Title>
            {result.imdb_id ? (
              <IMDB
                href={`https://www.imdb.com/title/${result.imdb_id}`}
                target="_blank"
              >
                <Img bgImage={require("../../assets/imdb.PNG")} />
              </IMDB>
            ) : null}
          </Header>
          <ItemContainer>
            <Item>
              {result.release_date
                ? result.release_date.substring(0, 4)
                : result.first_air_date
                ? result.first_air_date.substring(0, 4)
                : "‚ùå"}
            </Item>
            <Divider>‚ñ™</Divider>
            <Item>
              {result.runtime
                ? `${result.runtime} min`
                : result.episode_run_time
                ? `${result.episode_run_time[0]} min`
                : "‚ù§"}
            </Item>
            <Divider>‚ñ™</Divider>
            <Item>
              {result.genres &&
                result.genres.map((genre, index) =>
                  index === result.genres.length - 1
                    ? genre.name
                    : `${genre.name} / `
                )}
            </Item>
          </ItemContainer>

          <Overview>
            {result.overview.length > 500
              ? `${result.overview.slice(0, 500)}...`
              : result.overview}
          </Overview>

          <DetailTabContainer result={result} isMovie={isMovie} />
        </Data>
      </Content>
    </Container>
  );

  DetailPresenter.propTypes = {
    result: PropTypes.object,
    error: PropTypes.string,
    loading: PropTypes.bool.isRequired,
    isMovie: PropTypes.bool.isRequired,
  };
};

export default DetailPresenter;
