import React from "react";
import styled from "styled-components";

const Container = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  overflow: scroll; /*new*/
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
    height: 5px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
`;

const BoxContainer = styled.div`
  display: flex;
  align-items: center;
  margin: 20px; /*new*/
`;

const FlagContainer = styled.div`
  width: 300px;
  display: flex;
`;

const Flag = styled.img`
  /* width: 30%;
  margin: auto; */
  width: 100%;
  height: 150px;
  margin: auto;
  padding: 30px; /*new*/
  max-width: 250px; /*new*/
`;
const CountryContainer = styled.div`
  display: flex;
  align-items: center;
  margin: auto;
`;

const Country = styled.div`
  background-color: rgba(0, 0, 0, 0.4);
  width: 300px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 550;
  font-size: 25px;
`;

export default function TabCountry({ countries }) {
  return (
    <Container>
      {countries &&
        countries.map((country) => (
          <BoxContainer count={countries.length}>
            <FlagContainer>
              <Flag
                count={countries.length}
                src={
                  country.iso_3166_1
                    ? `https://www.countryflags.io/${country.iso_3166_1}/shiny/64.png`
                    : `${require("assets/NoImage.png")}`
                }
              />
            </FlagContainer>
            <CountryContainer>
              <Country count={countries.length}>{country.name}</Country>
            </CountryContainer>
          </BoxContainer>
        ))}
    </Container>
  );
}
